.proscons {
	display: flex;
	flex-direction: column;
	margin: 2.25rem auto 2rem;
	background: url('../img/pages/page-9-proscons-background.svg') no-repeat
		center center;

	@include mq($from: mobile-landscape) {
		padding-bottom: 6.25rem;
	}
	@include mq($from: tablet) {
		margin: 6.25rem auto 0;
	}
	@include mq($from: desktop) {
		margin: 6.25rem auto 1.5rem;
	}

	&__wrap-lists {
		margin-bottom: 2rem;

		@include mq($from: mobile-landscape) {
			display: flex;
			justify-content: space-between;
			margin-bottom: 1.25rem;
		}
	}

	&__wrap-list {
		&:not(:last-child) {
			margin-bottom: 2.75rem;

			@include mq($from: mobile-landscape) {
				margin-bottom: 0;
			}
		}

		@include mq($from: mobile-landscape) {
			flex: 0 0 calc(50% - 0.5rem);
			margin-bottom: 0;
		}
		@include mq($from: tablet-small) {
			flex: 0 0 calc(50% - 1.5rem);
		}

		// MODIFIERS
		&--pros {
			color: $c-pros;
		}
		&--cons {
			color: $c-cons;

			.proscons__add {
				@include mq($from: mobile-landscape) {
					margin-left: auto;
					flex-direction: row-reverse;
				}
			}
		}
	}

	&__list {
	}

	&__add {
		display: flex;
		justify-content: center;
		align-items: center;
		padding-left: 0;
		padding-right: 0;
		margin-bottom: 0.625rem;
		color: currentColor;
		border: 0;
		background-color: transparent;

		> svg {
			width: 1.75rem;
			height: 1.75rem;
			padding: 0.5rem;
			border: 1px solid;
			border-radius: 50%;
			background-color: currentColor;
		}
		> span {
			@include lhCrop(1.4);
			margin-left: 0.375rem;
			margin-right: 0.375rem;
			font-family: $ff-headings;
			font-size: 1.125rem;
			color: currentColor;
		}
	}

	&__item {
		color: currentColor;
		position: relative;

		&:not(:last-child) {
			margin-bottom: 0.5rem;
		}

		button {
			width: 1.25rem;
			height: 1.25rem;
			padding: 0.125rem;
			border: 0;
			background-color: transparent;
			position: absolute;
			top: 0.25rem;
			right: 0.25rem;
			z-index: 1;

			> svg {
				width: 100%;
				height: 100%;
			}
		}

		textarea {
			width: 100%;
			height: 3.75rem;
			padding: 0.625rem 1.5rem 0.625rem 1rem;
			border: 1px solid $c-border;
			border-radius: 0.25rem;
			color: currentColor;
			resize: none;

			&::placeholder {
				color: currentColor;
				font-size: 12px;
			}
		}
	}

	&__image {
		width: auto;
		margin: 0 auto 1.25rem;

		@include mq($from: tablet) {
			order: -1;
			margin-bottom: 0;
		}
	}

	&__finish {
		display: flex;
		justify-content: center;

		.button {
			> span {
				font-size: 1rem;
				padding-left: 3rem;
				padding-right: 3rem;
			}
		}
	}

	&__info {
		display: flex;
		flex-direction: column;
		align-items: center;
		position: absolute;
		top: 0;
		left: 50%;
		z-index: 1;
		transform: translate(-83px, 270px);

		@include mq($from: tablet) {
			width: 400px;
			align-content: flex-start;
			transform: translate(-170px, 550px);
		}

		.button + .button {
			margin-top: 1rem;

			@include mq($from: tablet) {
				margin-top: 3.25rem;
			}
		}
	}

	&__credits {
		margin-top: 1.5rem;
		text-align: center;

		@include mq($from: tablet) {
			margin-top: 0;
			font-size: 0.8125rem;
			text-align: left;
			transform: translate(240px, -130px);
		}
	}

	&__text {
		margin: 0;
		font-size: 0.8125rem;
		line-height: 1.6;
		color: $c-gray-100;
	}
}
