// -----------------------
// ARTICLE
// -----------------------

.article {
	$self: &;
	counter-reset: subchapter-counter;

	@include mq($from: desktop) {
		position: relative;
	}

	&__heading {
		margin-bottom: 0;
		text-align: center;

		+ #{$self}__title {
			margin-top: -0.5rem;
		}
	}

	&__title {
		margin-bottom: 1rem;
		color: $c-primary;
	}

	&__subtitle {
		margin-bottom: 2rem;
		font-family: $ff;
		font-size: 0.875rem;
		font-weight: $medium;
	}

	&__list {
		> li {
			margin-bottom: 1.875rem;
		}

		// MODIFIEERS
		&--sm {
			margin-bottom: 1.875rem;

			> li {
				margin-bottom: 0;
			}
		}
	}

	&__figure {
		width: 100%;
		max-width: 30rem;
		margin: 1.5rem auto 0.5rem;

		// MODIFIERS
		&--fixed {
			display: flex;
			justify-content: center;

			> img[width] {
				width: auto;
			}
		}
		&--small {
			width: 9.375rem;
			margin: 3rem auto;
		}
		&--medium {
			max-width: 27rem;
			margin: 3rem auto;
		}

		> figcaption {
		}

		> a {
			display: inline-block;
			font-size: 14px;
			text-align: center;
			color: $c-primary;
			transform: translateY(-20px);
		}
	}

	&__text {
		margin-bottom: 1.875rem;
	}
	&__source {
		margin-bottom: 1.875rem;
		font-size: 12px;
		text-align: center;
	}

	&__buttons {
		width: 9.5rem;
		height: 100%;
		padding-top: 16.375rem;
		text-align: center;
		position: absolute;
		left: 50%;
		transform: translateX(-50%);

		button:not(:first-child) {
	    margin-top: 15.125rem;

			@include mq($from: tablet) {
				margin-top: 14.875rem;
			}
		}
	}

	&__illustration {
		margin-top: 1.875rem;
		position: relative;
		left: 50%;
		transform: translateX(-50%);

		@include mq($from: desktop) {
			margin-top: 3.125rem;
		}
	}

	&__aside {
		pointer-events: none;
		overflow-y: hidden;
		overflow-x: hidden;

		@include mq($from: desktop) {
			display: flex;
			flex-direction: column;
			height: 100%;
			position: absolute;
			top: 0;
			left: 0;
			transform: translateX(-118%);
		}

		span {
			@include mq($from: desktop) {
				position: absolute;
				top: 0;
				right: 0;
				display: block;
				width: 1px;
				height: 44rem;
				opacity: 0.23;
				background: #bdbebe;
				background: linear-gradient(
					180deg,
					rgba(0, 0, 0, 1) 0%,
					rgba(134, 125, 125, 1) 70%,
					rgba(255, 255, 255, 1) 100%
				);
			}
		}

		img {
			display: none;

			@include mq($from: desktop) {
				display: block;
				width: auto;
			}
		}
	}
}
