.video {
  height: 100%;
  width: 100%;
  opacity: 0;
  background-color: var(--menu-background-mobile-color);
  transform: scale(0.7) translate(0, -100%);
  transition: all $normal $ease-in;
  pointer-events: none;

  // MODIFIERS
  &--in-place {
    opacity: 1;
    transform: none;
    margin-top: 2rem;
    margin-bottom: 2.5rem;

    @include mq($from: tablet) {
      margin-top: 4.25rem;
      margin-bottom: 4.25rem;
    }
  }

  // STATES
  &.is-visible {
    pointer-events: all;
    opacity: 1;
    transition: all $normal $ease-in;
    transform: none;
  }
}
