.message {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	padding: 1rem;
	position: fixed;
	z-index: 1050;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	background: rgba(30, 30, 30, 0.85);
}

.message p {
	max-width: 40rem;
	font-size: 1.5rem;
	color: $c-white;
	line-height: 1.4;
}
