// Dev
//-----------
$dev: #f06;
$dev-1: rgba(0, 255, 13, 0.74);
$dev-2: rgb(229, 255, 0);
$dev-3: rgb(0, 238, 255);

// Namespace
// -----------
$namespace: 'iot';

// Brand Colours
//-----------
$c-white: #ffffff;
$c-black: #2b2b2a;

$c-gray-100: #6e6e77;
$c-gray-200: #d0d0d7;
$c-gray-300: #f6f6f8;

$c-border: #e4e8ee;
$c-bg: #fdfdfd;

$c-primary: #239ca9;
$c-primary-lighten: #e7f8fa;
$c-secondary: #f6f0d8;

$c-success: $c-primary;
$c-error: #e95584;

$c-pros: $c-success;
$c-cons: $c-error;

:root {
	--wrapper-max-width: 100%;
}

// Easing
//-----------
$ease-in: cubic-bezier(0, 0, 0.24, 0.9);
$ease-out: cubic-bezier(0.59, 0, 0.68, 0.69);
$ease-in-bounce: cubic-bezier(0.03, 0.27, 0.49, 1.22);
$ease-out-bounce: cubic-bezier(0.59, 0, 0.59, 1.48);
$ease-linear: cubic-bezier(0.03, 0.27, 0.44, 0.93);
$ease-extreme: cubic-bezier(0.68, -0.55, 0.26, 1.55);

// Timing
//-----------
$fast: 150ms;
$normal: 250ms;
$slow: 350ms;
$ultraslow: 500ms;

// Delay
//-----------
$delay: 50ms;

// Z-index
//-----------
$z-layers: (
	bottomless-pit: -9999,
	behind: -1,
	default: 1,
	overlay: 100,
	modal: 200,
	header: 300,
	video: 350,
	spinner: 400,
);
