@mixin styleInputAndLabel($type) {
	$color: null;

	@if ($type == 'error') {
		$color: $c-error;
	} @else {
		$color: $c-success;
	}

	color: $color;

	input:focus ~ label {
		border-color: $color;

		&::before {
			background-image: url('../img/icons/custom-checkbox-#{$type}.svg');
		}
	}

	&.is-selected {
		pointer-events: none;

		input ~ label {
			border-color: $color;

			&::before {
				background-image: url('../img/icons/custom-checkbox-#{$type}.svg');
			}
		}
	}
}

.quiz {
	margin-top: 2rem;

	@include mq($from: mobile-landscape) {
		margin-top: 3rem;
		padding: 0 1.5rem;
	}

	&__image {
		width: auto;
	}

	&__stepper {
		margin-bottom: 1.25rem;
		text-align: center;

		&__legend {
			font-family: $ff-headings;
			font-size: 1rem;
			color: $c-primary;
			text-transform: uppercase;
		}

		&__bar {
			height: 0.5rem;
			border-radius: 0.25rem;
			background-color: #e3f7f9;
			box-shadow: -1px 1px 2px 0 rgba(#7f7f7f, 0.5) inset;
			position: relative;
			overflow: hidden;

			&::before {
				content: '';
				display: block;
				width: 100%;
				height: 100%;
				position: absolute;
				top: 0;
				left: 0;
				transform: translateX(calc(var(--barOffset) - 100%));
				background-color: #80e5ef;
				mix-blend-mode: multiply;
				transition: transform $slow $ease-in;
			}
		}
	}

	&__content {
	}

	&__header {
		margin: 0 auto 0.625rem;

		@include mq($from: mobile-landscape) {
			width: 75%;
		}
	}

	&__heading {
		font-size: 1.5rem;
		text-align: center;
	}

	&__body {
	}

	&__item {
		margin-bottom: 1.25rem;

		// MODIFIERS
		&[data-item='error'] {
			@include styleInputAndLabel('error');
		}

		&[data-item='success'] {
			@include styleInputAndLabel('success');
		}

		// STATES
		&.is-selected {
			.quiz__option {
				> label {
					box-shadow: 4px 4px 8px 0 rgba(#d3d3d3, 0);
				}
			}

			.quiz__feedback {
				display: block;
			}
		}
	}

	&__option {
		> label {
			display: block;
			padding: 0.875rem 0.75rem 0.875rem 2.5rem;
			font-size: 0.875rem;
			line-height: 1.8;
			color: $c-gray-100;
			border: 1px solid $c-gray-200;
			border-radius: 0.375rem;
			background-color: $c-white;
			box-shadow: 4px 4px 8px 0 rgba(#d3d3d3, 0.5);
			position: relative;

			&::before {
				content: '';
				position: absolute;
				width: 18px;
				height: 19px;
				top: 17px;
				left: 14px;
				background: url('../img/icons/custom-checkbox-default.svg') no-repeat
					center center;
			}
		}

		> input[type='radio'] {
			opacity: 0;
			position: absolute;
		}
	}

	&__feedback {
		display: none;
		padding: 1rem;
		margin-top: 0.25rem;
		border-radius: 0.375rem;
		text-align: center;
		background-color: currentColor;

		> svg {
			display: inline-block;
			width: 27px;
			height: 27px;
		}

		> p {
			width: 80%;
			margin: 0 auto;
			font-weight: $medium;
			color: $c-white;
		}
	}

	&__footer {
		margin: 3.5rem 0 2rem;
		text-align: center;

		@include mq($from: tablet) {
			margin-bottom: 0.5rem;
		}

		.button {
			margin: 0 0.5rem;
		}
	}
}
