// -----------------------
// HEADER
// -----------------------

.app-header {
	width: var(--wrapper-max-width);
	// height: 13.375rem;
	height: 4.3125rem;
	position: fixed;
	top: 0;
	left: 50%;
	z-index: 2;
	transform: translateX(-50%);

	&::before {
		content: '';
		display: block;
		width: 370px;
		height: 214px;
		position: absolute;
		top: 0;
		left: 50%;
		transform: translateX(-50%);
		background: url('../img/header.svg') no-repeat center 0;
		pointer-events: none;
	}

	@include mq($from: desktop) {
		--wrapper-max-width: 75rem;
	}

	// STATES
	&.is-open {
		display: flex;
		flex-direction: column;
		min-height: 100vh;
		// overflow-y: auto;
		height: 100vh;

		&::before,
		&::after {
			content: '';
			display: block;
			width: 100%;
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
		}

		&::before {
			height: 13.375rem;
			z-index: 0;
			transform: none;
			background: url('../img/header.svg') no-repeat center 0;
		}

		&::after {
			height: 100vh;
			z-index: -1;
			background-color: $c-bg;
		}
	}

	&__content {
		display: flex;
		justify-content: center;
		align-items: flex-start;
		height: 4.3125rem;
		position: relative;
		z-index: 1;
	}

	&__logo {
		margin: 1.25rem 1.25rem 1.25rem 3rem;

		> svg {
			display: block;
			width: 1.75rem;
			height: 1.8125rem;
		}
	}

	&__menu {
		padding: 0;
		margin: 1.25rem;
		border: 0;
		background-color: transparent;

		> svg {
			display: block;
			width: 2.1875rem;
			height: 1.75rem;
		}
	}
}
