.industry {
    padding-top: 8.75rem;
    background: $c-gray-300 url('../img/pages/page-4-background.svg') no-repeat center center / 533px auto;

    @include mq($from: tablet) {
        background-position: 50% 70%;
        background-size: 1100px auto;
    }

    .container {
        @include mq($from: tablet) {
            --wrapper-max-width: 44.5rem;
        }    
    }
    .content {
        @include mq($from: tablet) {
            width: 100%;
        }    
    }
}