// -----------------------
// RATING
// -----------------------

.rating {
  &__value {
    display: flex;
    align-items: baseline;
    margin-bottom: 0;

    > strong {
      font-size: 2rem;
      font-weight: $medium;
      color: $c-gray-100;

      @include mq($from: desktop) {
        font-size: 3.5rem;
      }
    }
    > span {
      margin-left: 0.5rem;
      font-size: 1.125rem;
      font-weight: $regular;
      color: $c-gray-200;

      @include mq($from: desktop) {
        font-size: 1.5rem;
      }
    }
  }

  &__progress {
    height: 0.5rem;
    border-radius: 0.25rem;
    opacity: 0.8;
    background-color: $c-gray-300;

    &::before {
      content: '';
      display: block;
      width: var(--progress-bar-width);
      height: 0.5rem;
      border-radius: 0.5rem;
      background-color: $c-success;
      will-change: width;
      transition: width 1500ms;
    }
  }
}
