body.menu-is-open {
  height: 100vh;
  overflow: hidden;
}

.navigation {
  pointer-events: none;
  opacity: 0;
  clip-path: polygon(0 100%, 0 100%, 100% 100%, 100% 100%);
  transform: translateY(0.5rem);
  transition: clip-path $normal $ease-in, opacity $normal,
    transform $ultraslow 100ms;
  
  // STATES
  .is-open & {
    flex-grow: 1;
    // overflow-y: auto;    
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 4rem;
    clip-path: polygon(0 0, 0 100%, 100% 100%, 100% 0);
    opacity: 1;
    pointer-events: all;
    transform: none;
    background: url('../img/navigation-bottom.svg') no-repeat center bottom;
    
    .navigation__item {
      opacity: 1;
      transform: none;
    }
  }

  &__list {
    padding-left: 0;
    list-style-type: none;
  }

  &__item {
    margin-bottom: 0.5em;
    text-align: center;
    opacity: 0;
    transform: translateY(0.5rem);
    transition: all $ultraslow;

    @for $i from 1 through 10 {
      &:nth-child(#{$i}) {
        transition-delay: $i * 50ms;
      }
    }

    @include mq($from: tablet) {
      opacity: 1;
      transform: none;
    }

    & + & {
      padding-top: 0.75rem;
      position: relative;

      &::before {
        content: '';
        display: block;
        width: 3.5rem;
        height: 0.0625rem;
        position: absolute;
        top: 0;
        left: 50%;
        transform: translateX(-50%);
        background-color: $c-gray-200;
      }      
    }
  }

  &__link {
    font-family: $ff-headings;
    font-size: 1rem;
    color: $c-gray-100;
    text-decoration: none;

    // STATES
    &.is-visited {
      + .navigation__icon {
        display: inline-block;
      }
    }
  }

  &__icon {
    display: none;
    transform: translate(0.25rem, 0.125rem);
  }
}
