*,
*:after,
*:before {
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	box-sizing: inherit;
	font-family: inherit;
}

html {
	-webkit-text-size-adjust: 100%;
	-ms-text-size-adjust: 100%;
	text-rendering: optimizeLegibility;
	font-family: $ff, Arial, sans-serif;
	font-size: 1em;
	line-height: 1.4;
	min-height: 100vh;
	box-sizing: border-box;
}

body {
	font-family: inherit;
	font-weight: normal;
	font-size: 100%;
	color: $c-gray-100;
	line-height: 1.4;
	min-height: 100vh;
	// padding-right: 15px;
}

abbr[title] {
	border-bottom: 0;
	text-decoration: none;
}

img,
iframe,
video {
	vertical-align: middle;
}

img {
	display: block;
	width: 100%;
	height: auto;
}

menu,
ul,
ol,
li,
dd {
	padding: 0;
	margin: 0;
	list-style-position: outside;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-weight: normal;
	margin-top: 0;
	margin-bottom: 0;
}

b,
strong {
	font-weight: $medium;
}

::-moz-selection {
	color: $c-gray-300;
	background-color: $c-primary;
}

::selection {
	color: $c-gray-300;
	background-color: $c-primary;
}

body *:focus {
	outline: 1px solid $c-primary;
	outline-offset: 2px;
	text-decoration: none;
}
body *:focus-visible {
}
