.ecosystem {
	padding-top: 8.75rem;

	.article {
		margin-bottom: 3.5rem;

		@include mq($from: tablet) {
			margin-bottom: 4.5rem;
		}
	}

	.article__title {
		&::before {
			counter-increment: subchapter-counter;
			content: '8.' counter(subchapter-counter) ' ';
		}
	}

	.article__hero {
		+ * {
			margin-top: -9rem;

			@include mq($from: tablet) {
				margin-top: -12rem;
			}
		}

		.tb {
			@include mq($from: tablet) {
				margin-top: 1.25rem;
				transform: translate(-615px, 0);
			}
			@include mq($from: desktop) {
				transform: translate(-595px, 0);
			}
		}
	}

	// BORDERS AND GRAPHICS WRAPPER
	.article__aside {
		@include mq($from: desktop) {
			height: calc(100% + 200px);
			transform: translateX(-234px);
		}
	}

	// GRAPHICS
	#stuff-1 {
		transform: translateY(995px);
	}

	// BORDERS
	#border-1 {
		transform: translateY(910px);
	}
}
