.smart-retail {
	padding-top: 8.75rem;

	.article__title {
		&::before {
			counter-increment: subchapter-counter;
			content: '6.' counter(subchapter-counter) ' ';
		}
	}

	.article__hero {
		+ * {
			margin-top: -10rem;

			@include mq($from: tablet) {
				margin-top: -16.5rem;
			}
		}

		.tb {
			@include mq($from: tablet) {
				transform: translate(-586px, 0);
			}
			@include mq($from: desktop) {
				transform: translate(-566px, 0);
			}
		}
	}

	.button-wrapper {
		// MODIFIERS
		&--three {
			margin-bottom: 5.5rem;
		}

		.button--image {
			background: url('../img/stuff-4.svg') no-repeat 0 0;
			position: relative;

			&::before {
				content: '';
				display: block;
				width: 69px;
				height: 75px;
				position: absolute;
				top: 33px;
				left: 50%;
				z-index: 1;
				transform: translateX(-50%);
				background: url('../img/stuff-5.svg') no-repeat 0 0;
			}

			> span {
				padding-left: 2rem;
				padding-right: 2rem;

				@include mq($from: desktop) {
					transform: translateX(-0.375rem);
				}
			}
		}

		@include mq($from: desktop) {
			position: static;
			transform: none;
		}
	}

	// BORDERS AND GRAPHICS WRAPPER
	.article__aside {
		transform: translateX(-210px);
	}

	// GRAPHICS
	#stuff-1 {
		transform: translateY(1035px);
	}
	#stuff-2 {
		transform: translate(38px, 1450px);
	}

	// BORDERS
	#border-1 {
		height: 1200px;
		transform: translateY(1070px);
	}
}
