.smart-cities {
	padding-top: 8.75rem;

	.article {
		position: relative;
	}

	.article__buttons {
		width: 10rem;
		height: auto;
		top: 40.9375rem;
		padding: 0;
		text-align: center;
		transform: translateX(-4.375rem);

		button:not(:first-child) {
			margin-top: 4rem;
		}

		@include mq($from: tablet) {
			top: 0;
			width: 100%;
			left: 0;
			transform: none;
			height: 100%;

			.air {
				transform: translate(-4px, 180px);
			}
			.parking {
				transform: translate(-210px, 365px);
			}
			.waste {
				transform: translate(185px, 190px);
			}
			.bikes {
				transform: translate(70px, 325px);
			}
		}
		@include mq($from: desktop) {
			.air {
				transform: translate(-155px, 270px);
			}
			.parking {
				transform: translate(-385px, 525px);
			}
			.waste {
				transform: translate(217px, 275px);
			}
			.bikes {
				transform: translate(405px, 360px);
			}
		}
	}

	~ #air-quality,
	~ #waste-management,
	~ #smart-parking,
	~ #smart-bikes {
		.modal__image {
			flex: 0 0 7.875rem;
			max-width: 7.875rem;
			padding-left: 2rem;
		}
	}
}
