.environment {
	padding-top: 8.75rem;

	.article {
		position: relative;
	}

	.article__heading {
		> span {
			display: block;

			@include mq($from: tablet) {
				display: inline-block;
			}
		}
	}

	.article__illustration {
		margin-top: -50px;
	}

	.article__buttons {
		width: 10rem;
		height: auto;
		top: 54.5rem;
		padding: 0;
		text-align: center;
		transform: translateX(-4.375rem);

		button:not(:first-child) {
			margin-top: 7rem;
		}

		@include mq($from: tablet) {
			top: 0;
			width: 100%;
			left: 0;
			transform: none;
			height: 100%;

			.bridge {
				transform: translate(30px, 190px);
			}
			.forest {
				transform: translate(-3px, 70px);
			}
			.water {
				transform: translate(70px, 130px);
			}
		}
	}

	~ #bridge-structural,
	~ #forest-fire,
	~ #water-quality {
		.modal__image {
			flex: 0 0 7.875rem;
			max-width: 7.875rem;
			padding-left: 2rem;
		}
	}
}
