.what-is {
	[role='main'] {
		margin-top: -9.375rem;
		position: relative;
		z-index: 1;

		@include mq($from: tablet) {
			margin-top: -4rem;
			position: static;
		}
		@include mq($from: desktop) {
			margin-top: -3rem;
		}
	}

	.app-header {
		display: none;
	}
	.article__heading {
		text-align: left;
	}

	.article__title {
		&::before {
			counter-increment: subchapter-counter;
			content: '1.' counter(subchapter-counter) ' ';
		}
	}

	.button-wrapper {
		&--one {
			@include mq($from: desktop) {
				transform: translate(-170px, 20px);
			}
		}
		&--two {
			@include mq($from: desktop) {
				transform: translate(-170px, -180px);
			}
		}
	}

	// BORDERS AND GRAPHICS WRAPPER
	.article__aside {
		transform: translateX(-260px);
	}

	// GRAPHICS
	#stuff-1 {
		transform: translateY(858px);
	}
	#stuff-2 {
		transform: translate(50px, 1550px);
	}
	#stuff-3 {
		transform: translate(14px, 2620px);
	}

	// BORDERS
	#border-1 {
	}
	#border-2 {
		transform: translateY(880px);
	}
	#border-3 {
		transform: translateY(1910px);
	}
	#border-4 {
		transform: translateY(3215px);
	}
}
