// ------------------------------------------------------
// TYPE
// ------------------------------------------------------

// Headings
// ------------------------------------------------------
h1,
h2,
h3,
h4,
h5,
h6 {
  margin-bottom: 1rem;
  font-family: $ff-headings;
  line-height: 1.5;
}

h1 {
  font-size: 2rem;
}

h2 {
  font-size: 1.5rem;
}

h3 {
  font-size: 1.25rem;
}

h4 {
  font-size: 1.125rem;
}

h5,
h6 {
  font-size: 1rem;
}

p {
  margin: 0;
  font-size: 0.875rem;
  line-height: 1.8;
  
  + p {
    margin-top: 2rem;
  }
}

ul {
  li {
    font-size: 0.875rem;
    line-height: 1.8;
    list-style-position: inside;
  }
}