// Sources
//-----------
$font-path: '../fonts' !default;

// @import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Roboto:ital,wght@0,400;0,500;1,300;1,400&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500&display=swap');

// Bebas Neue
// -----------------------------------------------------------------------------
@font-face {
	font-family: 'Bebas Neue Bold';
	src: url('#{$font-path}/Bebas-Neue-Pro-Bold.woff') format('woff');
	font-weight: 600;
	font-style: normal;
}

@font-face {
	font-family: 'Bebas Neue Regular';
	src: url('#{$font-path}/Bebas-Neue-Pro-Regular.woff') format('woff');
	font-weight: 400;
	font-style: normal;
}

// Fonts
//-----------
$ff: 'Roboto', sans-serif;
$ff-headings: 'Bebas Neue Bold', cursive;

// Font weight
//-----------
$light: 300;
$regular: 400;
$medium: 500;
