.how-works {
	padding-top: 8.75rem;

	.article__title {
		&::before {
			counter-increment: subchapter-counter;
			content: '2.' counter(subchapter-counter) ' ';
		}
	}

	.article__hero {
		+ * {
			margin-top: -14rem;
		}

		.dk {
			transform: translate(-595px, -0);
		}
	}

	.button-wrapper {
		&--one {
			@include mq($from: desktop) {
				transform: translate(-172px, -95px);
			}
		}
	}

	// BORDERS AND GRAPHICS WRAPPER
	.article__aside {
		transform: translateX(-260px);
	}

	// GRAPHICS
	#stuff-1 {
		transform: translateY(2350px);
	}

	// BORDERS
	#border-1 {
		transform: translateY(1930px);
	}
}
