.cover {
	position: relative;

	a {
		width: 48px;
		height: 43px;
		position: absolute;
		top: 0;
		left: 50%;
		z-index: 1;
		transform: translate(6px, 16px);

		@include mq($from: tablet) {
			width: 64px;
			height: 57px;
			transform: translate(2px, 30px);
		}
	}

	// ILLUSTRATIONS
	&__svg {
		position: relative;
		left: 50%;
		transform: translateX(-50%);

		// MODIFIERS
		&--mb {
			width: 717px;
			height: 1029px;
			transform: translateX(-53%);

			@include mq($from: tablet) {
				display: none;
			}
		}
		&--tb {
			display: none;
			width: 1106px;
			height: 1649px;

			@include mq($from: tablet) {
				display: block;
			}
		}
	}

	// EXCEPTIONS
	.button--primary {
		> span {
			padding-left: 2.125rem;
			padding-right: 2.125rem;
		}
	}

	&__info {
		display: flex;
		flex-direction: column;
		align-items: center;
		position: absolute;
		top: 0;
		left: 50%;
		z-index: 1;
		transform: translate(-83px, 270px);

		@include mq($from: tablet) {
			width: 400px;
			align-content: flex-start;
			transform: translate(-170px, 550px);
		}

		.button + .button {
			margin-top: 1rem;

			@include mq($from: tablet) {
				margin-top: 3.25rem;
			}
		}
	}

	&__credits {
		margin-top: 1.5rem;
		text-align: center;

		@include mq($from: tablet) {
			margin-top: 0;
			font-size: 0.8125rem;
			text-align: left;
			transform: translate(240px, -130px);
		}
	}

	&__text {
		margin: 0;
		font-size: 0.8125rem;
		line-height: 1.6;
		color: $c-gray-100;
	}
}
