.value-key {
	padding-top: 8.75rem;

	.article__hero {
		margin-top: 2.25rem;

		+ * {
			margin-top: -8rem;

			@include mq($from: tablet) {
				margin-top: -10rem;
			}
			@include mq($from: desktop) {
				margin-top: -12rem;
			}
		}
	}

	// BORDERS AND GRAPHICS WRAPPER
	.article__aside {
		transform: translateX(-200px);
	}

	// GRAPHICS
	#stuff-1 {
		transform: translateY(980px);
	}

	// BORDERS
	#border-1 {
		transform: translateY(1010px);
	}
}
