// Development styles
// ------------------------------------------------------
.dev {
  padding: 1rem;
  margin: 2rem 0;
  border-radius: 0.25rem;
  color: whitesmoke;
  background-color: #f06;
  box-shadow: 0 2px 2px 0 rgb(141, 0, 57);
}

// Display
// ------------------------------------------------------
%sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;

  &-focusable:active,
  &-focusable:focus {
    position:static;
    width:auto;
    height:auto;
    margin:0;
    overflow:visible;
    clip:auto;
  }
}

.sr-only {
  @extend %sr-only;
}

.d-none {
  display: none !important;
}

.d-inline-block {
  display: inline-block !important;
}

.d-block {
  display: block !important;
}

.d-flex {
  display: flex !important;
}

// Display responsive
// ------------------------------------------------------
.mb {
  @include mq($from: tablet) {
    display: none;
  }
}
.tb {
  display: none;

  @include mq($from: tablet) {
    display: block;
  }
  @include mq($from: desktop) {
    display: none;
  }
}
.dk {
  display: none;

  @include mq($from: desktop) {
    display: block;
  }
}


// Wrap
// ------------------------------------------------------
.wrap {
  flex-wrap: wrap !important;
}

// Alignment
// ------------------------------------------------------
.align-start {
  align-items: flex-start !important;
}

.justify-end {
  justify-content: flex-end !important;
}

.justify-between {
  justify-content: space-between !important;
}

// Visibility
// ------------------------------------------------------
.ovh {
  overflow: hidden !important;
}

// Offsets
// ------------------------------------------------------
$offset: (
  '0': 0,
  '0-5': 0.5rem,
  '1': 1rem,
  '1-5': 1.5rem,
  '2': 2rem,
  '2-5': 2.5rem,
  '3': 3rem,
  '3-5': 3.5rem,
  '4': 4rem,
  '4-5': 4.5rem,
  '5': 5rem,
  '5-5': 5.5rem,
);
$directions: (
  'default': null,
  't': 'top',
  'r': 'right',
  'b': 'bottom',
  'l': 'left',
);

@each $key, $value in $offset {
  @each $keyDir, $dir in $directions {
    @if ($keyDir == 'default') {
      .p-#{$key} {
        padding: $value !important;
      }
      .m-#{$key} {
        margin: $value !important;
      }
    } @else {
      .p#{$keyDir}-#{$key} {
        padding-#{$dir}: $value !important;
      }
      .m#{$keyDir}-#{$key} {
        margin-#{$dir}: $value !important;
      }
    }
  }
}

// Events
// ------------------------------------------------------
.no-events {
  pointer-events: none !important;
}

// Positions
// ------------------------------------------------------
.rel {
  position: relative !important;
}

// Text
// ------------------------------------------------------
.ttn {
  text-transform: none !important;
}
