body.modal-is-open {
	height: 100vh;
	overflow: hidden;
}

.modal {
	display: flex;
	justify-content: center;
	align-items: center;
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	z-index: z(modal);
	background-color: rgba(#ffffff, 0);
	pointer-events: none;
	transition: all $slow $ease-in;

	// MODIFIERS
	&--video {
		.modal-content {
			min-height: initial;
			padding: 0;
			border: 0;
		}
	}
	&--image {
		.modal-content {
			padding-top: 1rem;
			padding-bottom: 1rem;
		}

		.modal__inner {
			max-width: 48.75rem;
		}
	}
	&--companies {
		.modal-content {
			justify-content: center;
		}

		.modal__image {
			@include mq($from: mobile-landscape) {
				display: block;
			}
		}
		.modal__info {
			flex-grow: 0;
		}
	}
	&--unicorn,
	&--casino,
	&--cibersecurity {
		.modal__inner {
			max-width: 50rem;
		}

		.modal-content {
			display: block;
			padding-top: 2rem;
		}

		.modal__info {
			padding-top: 0;
			padding-bottom: 0;
		}

		.modal__info__image {
			width: 100%;
			margin: 2rem auto 0;

			@include mq($from: tablet) {
				width: auto;
			}
		}
	}
	&--quiz {
		.modal-content {
			padding-top: 2.5rem;
			text-align: center;

			@include mq($from: tablet) {
				padding-top: 2.5rem;
			}
		}
		.modal__info {
			padding-bottom: 0;
		}
		.modal__title {
			margin-bottom: 2.5rem;
			font-size: 1.625rem;
		}
		.modal__info__image {
    	max-width: 100%;
			margin: 0 auto;
		}
	}

	// STATES
	&.is-visible {
		pointer-events: auto;
		background-color: rgba(#ffffff, 0.9);

		.modal__inner {
			opacity: 1;
			transition: all $slow $ease-in;
			transform: none;
		}
	}

	&__inner {
		display: flex;
		flex-direction: column;
		width: 81.5vw;
		max-width: 42rem;
		height: 90vh;
		opacity: 0;
		transform: translateY(2rem);
		transition: all $slow $ease-in;

		@include mq($from: tablet) {
			transform: translateY(3rem);
		}
	}

	&-header {
		display: flex;
		justify-content: flex-end;
		height: 1.5rem;
		margin-bottom: 0.75rem;
	}

	&-content {
		display: flex;
		min-height: 23.75rem;
		padding: 2rem 1.25rem 0;
		border: 1px solid $c-primary;
		text-align: left;
		background-color: $c-white;
		box-shadow: 0.75rem 0.75rem 0.75rem 0 rgba(#e9e9e9, 0.5);
		overflow: auto;

		@include mq($from: tablet) {
			padding-top: 6.25rem;
		}
	}

	&__close {
		display: inline-block;
		padding: 0;
		border: none;
		background-color: transparent;
		transform: none;
		transition: transform $fast;

		// INTERACTIONS
		&:hover,
		&:focus {
			transition: transform $fast;
			transform: rotate(45deg);
		}

		// CHILDS
		> svg {
			display: inline-block;
			width: 1.5rem;
			height: 1.5rem;
		}
	}

	&__image {
		display: none;
		flex: 0 0 7.625rem;
		max-width: 7.625rem;
		border-right: 1px solid #d0d0d7;
		padding: 1.25rem 0 2.5rem;

		@include mq($from: tablet) {
			display: block;
		}

		// MODIFIERS
		&--full {
			display: block;
			flex-grow: 1;
			max-width: 100%;
			padding: 0;
			border: 0;
		}
	}

	&__video {
		width: 100%;
		height: 100%;
		object-fit: cover;
	}

	&__info {
		flex-grow: 1;

		@include mq($from: mobile-landscape) {
			padding: 0.625rem 1.5rem 2.5rem;
		}

		&__image {
			width: auto;
		}
	}

	&__title {
		margin-bottom: 1rem;
		font-family: $ff-headings;
		font-size: 1.25rem;
		color: $c-primary;
		text-transform: uppercase;
	}

	&__text {
		margin-bottom: 2px;
		color: $c-gray-100;
	}

	&__link {
		color: $c-primary;
	}

	&__list {
		margin: 1.5rem 0;
	}
}
