@mixin buttonException($height, $top, $left, $image) {
	.button--image {
		background: url('../img/stuff-4.svg') no-repeat 0 0;
		position: relative;

		&::before {
			content: '';
			display: block;
			width: 69px;
			height: $height;
			position: absolute;
			top: $top;
			left: $left;
			z-index: 1;
			transform: translateX(-50%);
			background: url(#{$image}) no-repeat 0 0;
		}

		> span {
			padding-left: 2rem;
			padding-right: 2rem;

			@include mq($from: desktop) {
				transform: translateX(-0.375rem);
			}
		}
	}

	@include mq($from: desktop) {
		position: static;
		transform: none;
	}
}

.challenges {
	padding-top: 8.75rem;

	.article__title {
		&::before {
			counter-increment: subchapter-counter;
			content: '9.' counter(subchapter-counter) ' ';
		}
	}

	.article__illustration {
		margin-top: 0;

		@include mq($from: tablet) {
			transform: translateX(-715px);
		}
		@include mq($from: desktop) {
			transform: translateX(-695px);
		}

		image {
			width: 1208px;

			@include mq($from: tablet) {
				width: auto;
			}
		}
	}

	.article__hero {
		+ * {
			margin-top: -14rem;

			@include mq($from: tablet) {
				margin-top: -16.5rem;
			}
		}
	}

	.button-wrapper {
		&--one {
			@include mq($from: desktop) {
				transform: translate(-172px, 30px);
			}
		}
		&--three {
			@include mq($from: desktop) {
				transform: translate(-172px, -460px);
			}
		}

		&--two {
			@include buttonException(75px, 33px, 50%, '../img/stuff-5.svg');
		}
		&--four {
			@include buttonException(110px, -5px, 42%, '../img/stuff-6.svg');
			margin: 4rem 0;
		}
		&--five {
			@include mq($from: desktop) {
				transform: translate(-172px, 300px);
			}
		}
	}

	// BORDERS AND GRAPHICS WRAPPER
	.article__aside {
		@include mq($from: desktop) {
			transform: translateX(-205px);
		}
	}

	// GRAPHICS
	#stuff-1 {
		transform: translateY(960px);
	}
	#stuff-2 {
		transform: translateY(3190px);
	}

	// BORDERS
	#border-1 {
		height: 65rem;
		transform: translateY(990px);
	}
	#border-2 {
		height: 55rem;
		transform: translateY(2340px);
	}
	#border-3 {
		height: 150rem;
		transform: translateY(3560px);
	}
}
