@mixin lhCrop($line-height) {
	&::before {
		content: '';
		display: block;
		height: 0;
		width: 0;
		margin-top: calc((#{$line-height} - 1) * 0.5em);
	}
}

// ----------------------------------------------------------------------
// BUTTONS
// ----------------------------------------------------------------------

@mixin button-base($color) {
	> span {
		@include lhCrop(1.4);
		padding: 0.5rem 1.5rem;
		border: 1px solid $color;
		font-family: $ff-headings;
		font-style: inherit;
		text-transform: uppercase;
		color: $color;
		background-color: $c-gray-300;
		transition: all 300ms;

		> svg {
			margin-left: 0.5rem;
		}
	}

	&:hover,
	&:focus {
		> span {
			color: $c-gray-300;
			background-color: $color;
		}
	}
}

.button-wrapper {
	text-align: center;
	margin-bottom: 2rem;

	@include mq($from: desktop) {
		position: absolute;
	}
}

// DEFAULT CLASS
.button {
	display: inline-flex;
	justify-content: center;
	align-items: center;
	padding: 0;
	font-size: 0.8125rem;
	border: 0;
	background-color: transparent;
	text-decoration: none;
	cursor: pointer;

	@include mq($from: tablet) {
		font-size: 1rem;
	}

	&[disabled] {
		pointer-events: none;
		opacity: 0.6;
	}

	// MODIFERS
	&--primary {
		@include button-base($c-gray-100);

		&.button--back {
			svg {
				margin-right: 0.5rem;
			}
		}
	}

	&--secondary {
		@include button-base($c-primary);
	}

	// &--icon {
	// 	svg {
	// 		margin-left: 0.375rem;
	// 	}
	// 	> span {
	// 		@include button-base($c-primary);
	// 		display: flex;
	// 		align-items: center;
	// 		padding: 0.5rem 1.5rem;
	// 	}
	// }

	// &--icon-secondary {
	// 	> span {
	// 		@include button-base($c-gray-100);
	// 	}
	// }

	&--image {
		@include button-base($c-gray-100);
		width: 8.625rem;
		height: 11rem;
		padding-top: 2.5rem;
		background: url('../img/button-maria-transparent.gif') no-repeat 20px 0;
		position: relative;

		&::before {
			content: '';
			display: block;
			width: 138px;
			height: 175px;
			background: url('../img/stuff-8.svg') no-repeat 0 0;
			position: absolute;
			top: 0;
			left: 0;
			z-index: -1;

			@include mq($from: desktop) {
				width: 146px;
				height: 130px;
				top: 3.125rem;
				background: url('../img/stuff-7.svg') no-repeat 0 0;
			}
		}

		@include mq($from: desktop) {
			width: 9.5rem;
			height: 10.625rem;
			background-position: 0 0;
		}

		> span {
			padding-left: 1rem;
			padding-right: 1rem;

			@include mq($from: desktop) {
				transform: translateX(-1.25rem);
			}
		}
	}

	&--footer {
		@include button-base($c-gray-100);
		font-size: 1rem;
		margin-left: 0.625rem;
		margin-right: 0.625rem;

		> span {
			padding-left: 2.5rem;
			padding-right: 2.5rem;
		}

		svg {
			display: inline-block;
		}

		&.button--left {
			svg {
				margin-right: 0.5rem;
			}
		}
		&.button--right {
			svg {
				margin-left: 0.5rem;
			}
		}
	}
}
